import {NgModule} from '@angular/core';
import {Routes, RouterModule, UrlSegment, UrlMatchResult} from '@angular/router';


const routes: Routes = [
    {
        path: '',
        loadChildren: () =>
            import('./landing/landing.module').then((m) => m.LandingModule),
    },
    {
        path: 'individuals-and-families',
        loadChildren: () =>
            import('./individuals-and-families/individuals-and-families.module').then((m) => m.IndividualsAndFamiliesModule),
    },
    {
        path: 'health-care-professionals',
        loadChildren: () =>
            import('./health-care-professionals/health-care-professionals.module').then((m) => m.HealthCareProfessionalsModule),
    },
    {
        path: 'health-services',
        loadChildren: () =>
            import('./health-services/health-services.module').then((m) => m.HealthServicesModule),
    },
    {
        matcher: contactUsMatcher,
        loadChildren: () =>
            import('./contact-us/contact-us.module').then((m) => m.ContactUsModule),
    },
    {
        path: 'about',
        loadChildren: () =>
            import('./about/about.module').then((m) => m.AboutModule),
    },
    {
        path: '**', // page not found
        loadChildren: () =>
            import('./not-found/not-found.module').then((m) => m.NotFoundModule),
    },
];

function contactUsMatcher(url: UrlSegment[]): UrlMatchResult | null {
    const professionals = 'health-care-professionals';
    const individuals = 'individuals-and-families';
    if (url.length === 2) {
        const pathPart1 = url[0].path;
        const pathPart2 = url[1].path;
        if ((pathPart1 === professionals || pathPart1 === individuals) && pathPart2.endsWith('contact-us')) {
            return ({consumed: url});
        }
    }
    return null;
}

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule {
}
